import ReactDOM from "react-dom";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import React, { Component } from "react";
import App from "../app.js";
/*  */
const temBuferSTANDART= JSON.stringify({ 
    news: { id_store: {}, st: [], len: 10, my_var: 10 },
    howThisWorks: { id_store: {}, st: [], len: 10, my_var: 10 }
 })
let language = window.navigator.language,
  temBufer = JSON.parse(temBuferSTANDART),
  lang,
  stor,
  storge = window.localStorage.getItem("userLang_ae_fin_obj"),
  config = {
    url_cms: "https://cms.fin.so",
    name_page: {
      Header: true,
      footer: true,
      Article: true,
      Contacts: true,
      Cooperation: true,
      DirectForm: true,
      faq: true,
      faqAnswer: true,
      Fees: true,
      HomePageDefault: true,
      howThisWorks: true,
      htmltojsx: true,
      News: true,
      Payment: true,
      Reviews: true,
      SignIn: true,
      SignUp: true
    }
  };
  if (storge) {
    // console.log(storge,"localStorage");
    try {
      stor = JSON.parse(storge);
    //   console.log(stor,"localStorage");
      config.localStorage = true;
    } catch (error) {
        console.error(error);
      f_lang();
    }
  } else {
    // console.log("NoDE localStorage");
    f_lang();
  }
  function f_lang() {
    if (/en/gim.test(language)) {
      lang = "en";
    } else {
      lang = "ru";
    }
    stor = {
      render: false,
      lang,
      page: {},
      lang_array: ["en", "ru", "cn"]
    };
    stor.lang_array.map((a, b) => {
      stor.page[a] = {};
      Object.keys(config.name_page).map((a_1, b_1) => {
        stor.page[a][a_1] = {};
        for (let i = 0; i < 100; i++) {
          stor.page[a][a_1]["r" + i] = "";
        }
      });
    });
  }
  function url(url) {
    url=url.replace(/[ \?\!\.\,]/gmi, '_')
  return "" + url;
}
function render(data) {
//  console.log("RENDER");

  class Root extends Component {
    
   
    
    render() {
      return (
        <Router>
          <Route exact path="/*" component={App} />
        </Router>
      );
    }
  }
  ReactDOM.render(<Root />, document.getElementById("root"));
}
// console.log(url, stor, config, render, temBufer);

export default { temBufer, url, stor, config, render,temBuferSTANDART };



