import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch,Link } from 'react-router-dom';
import My_router from "../../router/My_router";
//
import Loadable from "./Loadable/Load.js";
// page
import Faq from "./comp/faq.js";

class Faq_app extends Component {
  render() {
    const url = this.props.match.url.split("/");  
    if (url && Loadable[url[url.length - 1]]) {
      let Lob=Loadable[url[url.length - 1]];
        return <Lob page_name="faq" data={url[url.length - 1]} />;
    } else {
      return <Faq />;
    }
  }
}

export default Faq_app;



