import React, { Component } from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import My_router from "../router/My_router";
import Navigator from "./navigator/navigator.js";

// My_router.temBufer.news={};
// My_router.temBufer.news={id_store:{},st: [], len: 10, my_var: 10 };
// console.log(My_router,"My_router");
class News extends Component {
  constructor(props) {
    super(props);
    this.state = { st: [] };
  }
  componentDidMount() {
    this.my_fetch();
  }
  my_fetch(data) {
    data = data || 0;
    fetch(
      My_router.config.url_cms+`/news?_sort=updatedAt:desc&_start=${data}&_limit=10`,
      {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        }
      }
    )
      .then(response => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error("Something went wrong ...");
        }
      })
      .then(data => {
        data.map((a, b) => {
          //  console.log(My_router.temBufer.news.id_store);
          if ( !My_router.temBufer.news.id_store[a._id]) {
            // console.log("&&");
            
            My_router.temBufer.news.id_store[a._id] = true;
            My_router.temBufer.news.st.push({
              _id: a._id,
              body: a[My_router.stor.lang+"_body"],
              title: a[My_router.stor.lang+"_title"],
              img: a[My_router.stor.lang+"_img"] && a[My_router.stor.lang+"_img"].url ? My_router.config.url_cms + a[My_router.stor.lang+"_img"].url : "",
              updatedAt: a.updatedAt
            });
          }
        });
        // console.log(My_router.stor.lang,My_router.temBufer.news.st,"setState");
        
        this.setState({ st: My_router.temBufer.news.st });
      })
      .catch(error => {
        // console.log(error);
      });
  }
  render() {
    // console.log(this, "news");
    return (
      <main className="page-content news-page">
        <div className="layout">
          <div className="layout-mid">
            <Navigator
              url_arey={[
                {
                  to: My_router.url(""),
                  lun_text:My_router.stor.page[My_router.stor.lang].News.r1
                }
              ]}
              className="breadcrumbs mobile-padding"
              span={My_router.stor.page[My_router.stor.lang].News.r2}
            />
            <div className="page-title mobile-padding"  data-pageame="News.r2">{My_router.stor.page[My_router.stor.lang].News.r2}
            </div>
          </div>
        </div>
        <div className="layout mobile-padding">
          <div className="layout-mid">
            <div className="news-list">
              {/* {console.log(news,"news")} */}
              {this.state.st.map((a, b) => {
                // console.log(a);
                return (
                  <div className="item" key={b}>
                    <div >
                      <img src={a.img} />
                    </div>
                    <div className="content">
                      <div className="date" name={a}>
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width={22}
                          height={23}
                          viewBox="0 0 22 23"
                        >
                          <path
                            fill="#B4B4B7"
                            fillRule="nonzero"
                            d="M4 0v1H0v22h22V1h-4V0h-2v1H6V0H4zM2 3h2v1h2V3h10v1h2V3h2v2H2V3zm0 4h18v14H2V7zm6 2v2h2V9H8zm4 0v2h2V9h-2zm4 0v2h2V9h-2zM4 13v2h2v-2H4zm4 0v2h2v-2H8zm4 0v2h2v-2h-2zm4 0v2h2v-2h-2zM4 17v2h2v-2H4zm4 0v2h2v-2H8zm4 0v2h2v-2h-2z"
                          />
                        </svg>
                      </div>
                      <div className="title">{a.title}</div>
                      <div className="text">{a.body}</div>
                      <div className="more-info">
                        <a href="javascript:void(0);" className="more-info" data-pageame="News.r5"> {My_router.stor.page[My_router.stor.lang].News.r5}{" "}
                          &gt;
                        </a>
                      </div>
                    </div>
                    <div className="clear" />
                  </div>
                );
              })}
            </div>
            <div className="more-items">
              {(() => {
                if (this.state.st.length >= My_router.temBufer.news.len) {
                  My_router.temBufer.news.len = My_router.temBufer.news.len + My_router.temBufer.news.my_var;
                  return (
                    <a
                      href="javascript:void(0);"
                      className="button white"
                      onClick={() => {
                        this.my_fetch(My_router.temBufer.news.len - My_router.temBufer.news.my_var);
                      }}  data-pageame="News.r6" >{My_router.stor.page[My_router.stor.lang].News.r6}
                    </a>
                  );
                } else {
                  return "";
                }
              })()}
            </div>
          </div>
        </div>
      </main>
    );
  }
}

export default News;



