import React, { Component } from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import My_router from "../router/My_router";
import Navigator from "./navigator/navigator.js";
class howThisWorks extends Component {
  constructor(props) {
    super(props);
    this.state = { st: [
      {
          "_id": "",
          "en_title": "",
          "cn_title": "",
          "en_video": "",
          "cn_video": "",
          "cn_body": "",
          "en_body": "",
          "ru_title": "",
          "ru_body": "",
          "ru_video": "",
          "__v": 0,
          "id": "",
          "en_img": {

              "url": "",

          },
          "ru_img": {

              "url": "",
   
          },
          "cn_img": {

              "url": "",

          }
      }
  ] };
  }
  componentDidMount() {
    this.my_fetch();
  }
  my_fetch(data) {
    data = data || 0;
    fetch(My_router.config.url_cms + "/howThisWorks", {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      }
    })
      .then(response => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error("Something went wrong ...");
        }
      })
      .then(data => {
        data.map((a, b) => {
          // console.log(My_router.temBufer.howThisWorks.id_store);
          if (!My_router.temBufer.howThisWorks.id_store[a._id]) {
            // console.log("&&");

            My_router.temBufer.howThisWorks.id_store[a._id] = true;
            My_router.temBufer.howThisWorks.st.push({
              _id: a._id,
              body: a[My_router.stor.lang + "_body"],
              title: a[My_router.stor.lang + "_title"],
              video: a[My_router.stor.lang + "_video"],
              img:
                a[My_router.stor.lang + "_img"] &&
                a[My_router.stor.lang + "_img"].url
                  ? My_router.config.url_cms +
                    a[My_router.stor.lang + "_img"].url
                  : "",
              updatedAt: a.updatedAt
            });
          }
        });
        /* console.log(
          My_router.stor.lang,
          My_router.temBufer.howThisWorks.st,
          "setState"
        ); */

        this.setState({ st: My_router.temBufer.howThisWorks.st });
      })
      .catch(error => {
        // console.log(error);
      });
  }
  render() {
    return (
      <main className="page-content how-this-works-page">
        <div className="layout">
          <div className="layout-mid">
            <Navigator
              url_arey={[
                {
                  to: My_router.url(""),
                  lun_text:
                    My_router.stor.page[My_router.stor.lang].howThisWorks.r1
                }
              ]}
              className="breadcrumbs mobile-padding"
              span={My_router.stor.page[My_router.stor.lang].howThisWorks.r2}
            />
            <div className="page-title mobile-padding">
              {this.state.st[0].title}
            </div>
            <div className="steps mobile-padding">
              <div className="step video">
                <div className="content">
                  <div className="iframe-wrapper">
                    <iframe
                      width={570}
                      height={321}
                      src={this.state.st[0].video}
                      frameBorder={0}
                      allowFullScreen
                    />
                  </div>
                  <div className="text">{this.state.st[0].body}</div>
                </div>
              </div>
              
              {this.state.st.map((a, b) => {
                return (
                  <div  key={b}>
{
                  (() => {
                      if (b > 0) {
                        return (
                          <div className="step" key={b}>
                          <div  className="title">
                       {a.title+" - "+b}
                   </div>
                            <div className="content">
                              <div className="image-wrapper">
                                <img 
                                src={a.img} />
                              </div>
                              <div className="text">{a.body}</div>
                            </div>
                          </div>
                        );
                      }
                    })()}
                  </div>
                );
              })}
       
        </div>
        </div>
        </div>
      </main>
    );
  }
}

export default howThisWorks;



